.token-info-page {
  padding: 2rem;
  max-width: 1200px;
  margin: 0 auto;
}

.token-info-page h1 {
  text-align: center;
  color: #333;
  margin-bottom: 2rem;
}

.tokens-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
}

.token-card {
  background: white;
  border-radius: 12px;
  padding: 1.5rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.token-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
}

.token-card h2 {
  color: #333;
  margin-bottom: 1rem;
  text-align: center;
}

@media (max-width: 768px) {
  .token-info-page {
    padding: 1rem;
  }

  .tokens-grid {
    grid-template-columns: 1fr;
  }
} 