.token-metadata {
  background: #f8f9fa;
  border-radius: 8px;
  padding: 1rem;
  margin: 1rem 0;
}

.metadata-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 0;
  border-bottom: 1px solid #eee;
}

.metadata-row:last-child {
  border-bottom: none;
}

.metadata-label {
  color: #666;
  font-size: 0.9rem;
}

.metadata-value {
  font-weight: 500;
  color: #333;
}

.token-metadata.loading {
  text-align: center;
  color: #666;
  font-style: italic;
  animation: pulse 1.5s infinite;
}

.token-metadata.error {
  text-align: center;
  color: #dc3545;
  font-style: italic;
}

@keyframes pulse {
  0% { opacity: 1; }
  50% { opacity: 0.5; }
  100% { opacity: 1; }
} 