.notification {
  position: fixed;
  top: 20px;
  right: 20px;
  padding: 1rem;
  border-radius: 8px;
  display: flex;
  align-items: center;
  gap: 1rem;
  animation: slideIn 0.3s ease;
  z-index: 1000;
  min-width: 300px;
  max-width: 500px;
}

.notification.info {
  background: #e3f2fd;
  color: #1976d2;
  border: 1px solid #1976d2;
}

.notification.success {
  background: #e8f5e9;
  color: #2e7d32;
  border: 1px solid #2e7d32;
}

.notification.error {
  background: #fdecea;
  color: #d32f2f;
  border: 1px solid #d32f2f;
}

.notification-message {
  flex: 1;
  font-size: 0.9rem;
}

.notification-close {
  background: none;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
  padding: 0;
  color: inherit;
  opacity: 0.7;
  transition: opacity 0.2s ease;
}

.notification-close:hover {
  opacity: 1;
}

@keyframes slideIn {
  from {
    transform: translateX(100%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

@media (max-width: 768px) {
  .notification {
    top: auto;
    bottom: 20px;
    left: 20px;
    right: 20px;
    min-width: auto;
  }
} 