.create-lottery-container {
  max-width: 600px;
  margin: 0 auto;
  padding: 2rem;
  background: white;
  border-radius: 12px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.create-lottery-container h1 {
  text-align: center;
  color: #333;
  margin-bottom: 2rem;
}

.create-lottery-form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.form-group label {
  font-weight: 600;
  color: #333;
}

.form-group input {
  padding: 0.8rem;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 1rem;
}

.submit-button {
  padding: 1rem;
  background: linear-gradient(45deg, #6d0b91, #4e1d7b);
  color: white;
  border: none;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.submit-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(109, 11, 145, 0.2);
}

.token-info {
  margin-top: 0.5rem;
  padding: 0.5rem;
  background: #f8f9fa;
  border-radius: 4px;
  font-size: 0.9rem;
}

.token-info span {
  display: block;
  color: #666;
  margin: 2px 0;
}

select {
  width: 100%;
  padding: 0.8rem;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 1rem;
  background-color: white;
  cursor: pointer;
}

select:focus {
  outline: none;
  border-color: #6d0b91;
  box-shadow: 0 0 0 2px rgba(109, 11, 145, 0.2);
}

.form-group {
  position: relative;
}

.form-group .error-message {
  color: #dc3545;
  font-size: 0.8rem;
  margin-top: 0.25rem;
}

.form-group .info-message {
  color: #0d6efd;
  font-size: 0.8rem;
  margin-top: 0.25rem;
} 