.token-price {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem;
  background: #f8f9fa;
  border-radius: 8px;
  font-size: 0.9rem;
}

.price-label {
  color: #666;
}

.price-value {
  font-weight: 600;
  color: #6d0b91;
}

.token-price.loading {
  color: #666;
  font-style: italic;
}

.token-price.error {
  color: #dc3545;
  font-style: italic;
}

@keyframes pulse {
  0% { opacity: 1; }
  50% { opacity: 0.5; }
  100% { opacity: 1; }
}

.token-price.loading {
  animation: pulse 1.5s infinite;
} 