.contract-manager-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 2rem;
}

.contract-manager-container h1 {
  text-align: center;
  color: #333;
  margin-bottom: 2rem;
}

.info-section {
  background: white;
  padding: 1.5rem;
  border-radius: 12px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  margin-bottom: 2rem;
}

.info-item {
  display: flex;
  justify-content: space-between;
  padding: 0.8rem 0;
  border-bottom: 1px solid #eee;
}

.info-item:last-child {
  border-bottom: none;
}

.info-item span:first-child {
  font-weight: 600;
  color: #333;
}

.info-item span:last-child {
  font-family: monospace;
  color: #666;
  word-break: break-all;
}

.actions-section {
  margin-top: 2rem;
}

.actions-section h2 {
  color: #333;
  margin-bottom: 1.5rem;
}

.action-card {
  background: white;
  padding: 1.5rem;
  border-radius: 12px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  margin-bottom: 1.5rem;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.action-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
}

.action-card h3 {
  color: #333;
  margin-bottom: 1rem;
}

.form-group {
  display: flex;
  gap: 1rem;
  margin-bottom: 1rem;
}

.form-group input {
  flex: 1;
  padding: 0.8rem;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 1rem;
}

.form-group input:focus {
  outline: none;
  border-color: #6d0b91;
  box-shadow: 0 0 0 2px rgba(109, 11, 145, 0.2);
}

.action-button {
  padding: 0.8rem 1.5rem;
  background: linear-gradient(45deg, #6d0b91, #4e1d7b);
  color: white;
  border: none;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  min-width: 120px;
}

.action-button:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(109, 11, 145, 0.2);
}

.action-button:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

.details-display {
  background: #f8f9fa;
  padding: 1.5rem;
  border-radius: 8px;
  margin-top: 1rem;
}

.details-display h4 {
  color: #333;
  margin-bottom: 1rem;
}

.details-display p {
  margin: 0.5rem 0;
  color: #666;
}

.details-display p span {
  font-weight: 500;
  color: #333;
}

@media (max-width: 768px) {
  .contract-manager-container {
    padding: 1rem;
  }

  .form-group {
    flex-direction: column;
  }

  .action-button {
    width: 100%;
  }
}

/* Loading ve Error durumları için stiller */
.loading-container,
.error-container {
  text-align: center;
  padding: 2rem;
  background: white;
  border-radius: 12px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.loading-spinner {
  margin: 0 auto;
  margin-bottom: 1rem;
}

.error-message {
  color: #dc3545;
  margin-bottom: 1rem;
}

.retry-button {
  padding: 0.5rem 1rem;
  background: #dc3545;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.retry-button:hover {
  background: #c82333;
} 