.lottery-list {
  width: 70%; /* %85 - %15 = %70 genişlik */
  margin-left: 15%; /* Sol taraftan %15 boşluk */
  margin-right: 15%; /* Sağ taraftan %15 boşluk */
  display: flex;
  flex-direction: column; /* Dikey yönde düzenleme */
  gap: 20px;
  padding: 20px 0;
}

.lottery-card {
  width: 100%; /* Her kart tam genişlikte olacak */
  background: white;
  border-radius: 12px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  padding: 20px;
  margin-bottom: 20px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  position: relative;
  overflow: hidden;
}

.lottery-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
}

.lottery-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(45deg, #6d0b91, #4e1d7b);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.lottery-card:hover::before {
  opacity: 1;
}

.card-top {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 15px;
  position: relative;
}

.token-section {
  flex: 1;
  display: flex;
  gap: 10px;
}

.token-logo {
  width: 40px;
  height: 40px;
}

.token-info {
  flex: 1;
}

.token-name-wrapper {
  display: flex;
  flex-direction: column;
}

.token-name-wrapper h2 {
  margin: 0;
  font-size: 1.1rem;
}

.token-symbol {
  font-size: 0.8rem;
  color: #666;
}

.lottery-id {
  position: absolute;
  right: 0;
  top: -15px;
  color: #666;
  font-size: 0.8rem;
}

.price-section {
  text-align: right;
  margin-left: 30px;
  min-width: 120px;
  margin-top: 15px;
}

.price-tag {
  color: #666;
  font-size: 0.8rem;
  margin-bottom: 4px;
}

.price-value {
  font-weight: 600;
  color: #333;
  font-size: 1rem;
}

.progress-section {
  margin: 1rem 0;
}

.progress-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.5rem;
}

.progress-numbers {
  font-size: 0.9rem;
  color: #666;
  font-weight: 500;
}

.progress-bar {
  width: 100%;
  height: 8px;
  background: #f0f0f0;
  border-radius: 4px;
  overflow: hidden;
  position: relative;
}

.progress-fill {
  height: 100%;
  background: linear-gradient(45deg, #6d0b91, #4e1d7b);
  border-radius: 4px;
  transition: width 0.3s ease;
  position: relative;
}

.progress-percent {
  position: absolute;
  right: 4px;
  top: -18px;
  font-size: 0.75rem;
  color: #6d0b91;
  font-weight: 500;
}

.join-button {
  width: 100%;
  padding: 0.8rem;
  background: linear-gradient(45deg, #6d0b91, #4e1d7b);
  color: white;
  border: none;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 1rem;
}

.join-button:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(109, 11, 145, 0.2);
}

.join-button:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

.status-section {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-top: 1rem;
  padding-top: 1rem;
  border-top: 1px solid #eee;
}

.status-badge {
  padding: 0.25rem 0.75rem;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 600;
}

.status-badge.active {
  background: rgba(25, 135, 84, 0.1);
  color: #198754;
}

.status-badge.ended {
  background: rgba(108, 117, 125, 0.1);
  color: #6c757d;
}

.time-info {
  font-size: 0.9rem;
  color: #666;
}

.action-section {
  margin-top: 1rem;
  display: flex;
  justify-content: center;
}

.loading-spinner {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}

.spinner {
  width: 16px;
  height: 16px;
  border: 2px solid #ffffff;
  border-top: 2px solid transparent;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Token bilgisi için yeni stiller */
.token-info-section {
  margin-top: 1rem;
  padding: 1rem;
  background: #f8f9fa;
  border-radius: 8px;
}

.token-info-row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.5rem;
}

.token-info-label {
  color: #666;
  font-size: 0.9rem;
}

.token-info-value {
  font-weight: 500;
  color: #333;
}

/* Responsive tasarım için */
@media (max-width: 768px) {
  .lottery-list {
    width: 90%;
    margin-left: 5%;
    margin-right: 5%;
  }
}

.address-chips {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.address-chip {
  padding: 4px 8px;
  border-radius: 12px;
  font-size: 0.8rem;
  font-family: monospace;
}

.address-chip.current-user {
  background-color: rgba(255, 223, 0, 0.2);
  color: #b8860b;
  font-weight: 600;
}

.address-chip.other-user {
  background-color: #f5f5f5;
  color: #333;
}

.empty-state {
  text-align: center;
  padding: 3rem;
  background: white;
  border-radius: 12px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.empty-icon {
  font-size: 3rem;
  margin-bottom: 1rem;
}

.empty-state h3 {
  color: #333;
  margin-bottom: 0.5rem;
}

.empty-state p {
  color: #666;
}

.participation-badge {
  background: rgba(25, 135, 84, 0.1);
  color: #198754;
  padding: 0.5rem 1rem;
  border-radius: 20px;
  font-size: 0.9rem;
  font-weight: 500;
  text-align: center;
}

.countdown-label {
  color: #666;
  font-size: 0.9rem;
  margin-bottom: 0.5rem;
  text-align: center;
}

/* Responsive tasarım için ek stiller */
@media (max-width: 768px) {
  .lottery-card {
    margin: 1rem 0;
  }

  .card-top {
    flex-direction: column;
    gap: 1rem;
  }

  .price-section {
    text-align: left;
    margin-left: 0;
  }
} 