.header-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
}

.refresh-button {
  padding: 0.5rem 1rem;
  background: linear-gradient(45deg, #6d0b91, #4e1d7b);
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 600;
  transition: all 0.3s ease;
}

.refresh-button:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(109, 11, 145, 0.2);
}

.refresh-button:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

.loading-message,
.error-message {
  text-align: center;
  padding: 2rem;
  border-radius: 8px;
  background: #f8f9fa;
  margin: 1rem 0;
}

.error-message {
  color: #dc3545;
  background: #f8d7da;
} 