.not-found-container {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: calc(100vh - 100px);
  padding: 2rem;
}

.not-found-content {
  text-align: center;
  background: white;
  padding: 3rem;
  border-radius: 12px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  max-width: 500px;
  width: 100%;
}

.not-found-icon {
  font-size: 6rem;
  font-weight: bold;
  color: #6d0b91;
  margin-bottom: 1rem;
  background: linear-gradient(45deg, #6d0b91, #4e1d7b);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
}

.not-found-content h1 {
  color: #333;
  margin-bottom: 1rem;
}

.not-found-content p {
  color: #666;
  margin-bottom: 2rem;
}

.home-button {
  display: inline-block;
  padding: 0.8rem 2rem;
  background: linear-gradient(45deg, #6d0b91, #4e1d7b);
  color: white;
  text-decoration: none;
  border-radius: 8px;
  font-weight: 600;
  transition: all 0.3s ease;
}

.home-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(109, 11, 145, 0.2);
}

@media (max-width: 768px) {
  .not-found-container {
    padding: 1rem;
  }

  .not-found-content {
    padding: 2rem;
  }

  .not-found-icon {
    font-size: 4rem;
  }
} 