.completed-lotteries-page {
  padding: 2rem;
  max-width: 1200px;
  margin: 0 auto;
}

.header-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
}

.header-section h1 {
  color: #333;
  margin: 0;
}

.refresh-button {
  padding: 0.5rem 1.5rem;
  background: linear-gradient(45deg, #6d0b91, #4e1d7b);
  color: white;
  border: none;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.refresh-button:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(109, 11, 145, 0.2);
}

.refresh-button:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

@media (max-width: 768px) {
  .completed-lotteries-page {
    padding: 1rem;
  }
} 