.countdown {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin: 1rem 0;
}

.countdown-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 60px;
}

.countdown-value {
  font-size: 1.5rem;
  font-weight: 600;
  color: #6d0b91;
}

.countdown-label {
  font-size: 0.8rem;
  color: #666;
  text-transform: uppercase;
}

@media (max-width: 768px) {
  .countdown {
    gap: 0.5rem;
  }

  .countdown-item {
    min-width: 50px;
  }

  .countdown-value {
    font-size: 1.2rem;
  }

  .countdown-label {
    font-size: 0.7rem;
  }
} 